@import url(/src/components/fonts/Bleeding_Cowboys.ttf);
@import url(/src/components/fonts/Bellefair-Regular.ttf);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #efefef;
}
.page {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.card {
  box-shadow: 10px 10px 10px rgba(5, 3, 3, 0.1);
  margin: 20px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 250px;
  display: inline-block;
  border: 1px solid black;
  border-radius: 10px;
  background-color: white;
  padding-bottom: 15px;
  max-height: 300px;
}

.title {
  text-align: center;
  width: 100%;
  font-family: "Bleeding Cowboys";
  font-weight: 100;
  letter-spacing: 5px;
  font-size: 25px;
}
.neo {
  padding: 2px 10px;
  font-family: "Bellefair";
  line-height: 23px;
  font-size: 18px;
}

.date {
  font-weight: 600;
  text-transform: uppercase;
  color: limegreen;
  text-decoration: underline;
  letter-spacing: 1px;
}

.diam {
  font-weight: 600;
  color: magenta;
  text-decoration: underline;
}

.imageCard {
  box-shadow: 10px 10px 10px rgba(5, 3, 3, 0.1);
  margin: 20px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  display: inline-block;
  background-color: white;
  max-width: 100vw;
  max-height: 80vh;
  overflow: hidden;
}

.loading {
  background-color: black;
  width: 600px;
  height: 300px;
  margin: 30px;
  display: inline-block;
}

.container {
  padding: 10px;
  padding-top: 30px;
  width: 100%;
  font-family: monospace;
  color: limegreen;
  font-size: 15px;
  font-weight: 100;
  overflow: hidden;
  -webkit-animation: anime-one 4s forwards;
          animation: anime-one 4s forwards;
}

.detailsCard {
  box-shadow: 10px 10px 10px rgba(5, 3, 3, 0.1);
  margin: 20px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  display: inline-block;
  max-width: 400px;
  padding-bottom: 15px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.title {
  text-align: center;
  width: 100%;
  font-family: "Bleeding Cowboys";
  font-weight: 100;
  letter-spacing: 5px;
  font-size: 25px;
}

@-webkit-keyframes anime-one {
  0% {
    height: 40px;
  }
  50% {
    height: 100px;
  }
  100% {
    height: -webkit-fit-content;
    height: fit-content;
  }
}

@keyframes anime-one {
  0% {
    height: 40px;
  }
  50% {
    height: 100px;
  }
  100% {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
}

.navbar {
  left: 0;
  font-family: "Bellefair";
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  font-size: 20px;
  box-shadow: 0px 10px 10px rgba(5, 3, 3, 0.1);
  background-color: #f9f9f9;
}
nav a {
  text-decoration: none;
  color: black;
}
.stlist {
  list-style: none;
  display: inline-block;
  text-align: center;
  padding: 20px 5px 20px 5px;
  line-height: 0;
  text-transform: lowercase;
  -webkit-text-decoration: underline orange;
          text-decoration: underline orange;
}

.footer {
  width: 100%;
  min-height: 250px;
  background-color: #333;
  margin-top: 50px;
  font-family: "Bellefair";
  font-size: 20px;
  color: white;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.copyright {
  width: 100%;
  padding-top: 10px;
  min-height: 50px;
  background-color: #222222;
  text-align: center;
  color: white;
  text-transform: lowercase;
  font-family: "Bleeding Cowboys";
  letter-spacing: 4px;
  font-size: 15px;
}

.andCopy {
  font-family: "Bellefair";
  font-weight: 600;
}

.About {
  padding: 20px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  display: inline-block;
  overflow: hidden;
  width: 700px;
  max-width: 100%;
  text-align: justify;
}

.Products {
  padding: 20px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  display: inline-block;
  overflow: hidden;
  max-width: 100%;
  text-align: justify;
  text-transform: lowercase;
  padding-left: 25px;
}

.headingAbout {
  text-align: center;
  width: 100%;
  font-family: "Bleeding Cowboys";
  font-weight: 100;
  letter-spacing: 5px;
  font-size: 20px;
  text-transform: uppercase;
  margin-bottom: 10px;
  border-bottom: white 1px solid;
}

.Products li {
  line-height: 25px;
  -webkit-text-decoration: underline orange;
          text-decoration: underline orange;
  font-size: 25px;
  list-style: circle;
}

